/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box; }

body {
  font-family: 'Roboto', sans-serif; }

img {
  vertical-align: middle; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.col-2 {
  width: 50%; }

.col-3 {
  width: 33.33333%; }

.font-20 {
  font: italic normal 500 20px/30px Roboto; }

.font-24 {
  font: italic normal 700 24px/32px Roboto; }

.font-64 {
  font: italic normal 500 64px/84px Roboto; }

.font-80 {
  font: italic normal 900 80px/106px Roboto; }

.text-center {
  text-align: center; }

.mr-6 {
  margin-right: 24px; }

.mb-1 {
  margin-bottom: 4px; }

.mb-2 {
  margin-bottom: 8px; }

.mb-3 {
  margin-bottom: 12px; }

.mb-4 {
  margin-bottom: 16px; }

.mb-5 {
  margin-bottom: 20px; }

.mb-6 {
  margin-bottom: 24px; }

.mb-7 {
  margin-bottom: 28px; }

.mb-8 {
  margin-bottom: 32px; }

.mb-9 {
  margin-bottom: 36px; }

.mb-10 {
  margin-bottom: 40px; }

.mb-12 {
  margin-bottom: 48px; }

.mb-14 {
  margin-bottom: 56px; }

.mb-17 {
  margin-bottom: 68px; }

.header {
  height: 958px;
  background: url(../images/banner.png) center center no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center; }
  .header-content {
    color: #fff; }
    .header-content p {
      max-width: 283px;
      margin-left: 16.5px; }
  .header-link img {
    margin: 0 8px; }
    .header-link img:hover {
      transform: scale(1.1); }

.about {
  max-width: 412px; }
  .about .about-skills {
    border-top: 1px solid #000;
    padding-top: 28px;
    margin: 0 14px;
    max-width: 384px;
    text-align: center; }
    .about .about-skills h3::before {
      content: '';
      display: inline-block;
      border-radius: 100%;
      width: 22px;
      height: 22px;
      margin-right: 16px;
      background: #891818; }

.brand {
  background: #000;
  color: #fff;
  padding-top: 56px;
  padding-bottom: 160px; }
  .brand-img {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }

.works-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.footer {
  background: #000;
  height: 310px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .footer .contact {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #fff;
    width: 315px; }
    .footer .contact a {
      display: block;
      color: #fff;
      font: italic normal 500 24px/36px Roboto;
      text-decoration: none; }
